import request from './request.js'

import store from '@/store'
import { getToken } from '@/utils'

/**
 * 重置各种次数
 * @param {String} zhanghao - 用户账号
 * @param {String} mima - 用户密码
 * @param {String} token -用户唯一标识
 * @returns
 */
 export const resetgezhongcishuApi = async () => {
  //获取最新的token到state
  await getToken()
  return request({
    url:'/chongzhijinglidan.d',
    method:'post',
    params:{
      'zhanghao':store.state.user.account.zhanghao?store.state.user.account.zhanghao:'',
      'mima': store.state.user.account.mima?store.state.user.account.mima:'',
      'token':store.state.user.token?store.state.user.token:''
    }
  })
}


