import NavBar from '@/components/navbar.vue';
import { onMounted, computed } from 'vue';
import xiuweiJson from '@/assets/data/xiuwei.json';
import menpaiJson from '@/assets/data/menpai.json';
import { getWorldChatApi } from '@/api/function';
import { useStore } from 'vuex';
export default {
  name: '',
  components: {
    NavBar
  },

  setup() {
    const store = useStore(); //存储自己门派的id

    let myMenpaiId = computed(() => {
      return store.getters['user/getMenpaiId'];
    }); //存储定时器

    let timer = null; //开器定期器 获取世界聊天

    const startTimer = () => {
      clearInterval(timer);
      timer = setInterval(async () => {
        //发送请求获取世界聊天
        let result = await getWorldChatApi(); //判断返回是否有数据

        if (result !== 1 && result.indexOf(',,') !== -1) {
          //将消息字符串 通过,,分割称数组
          let messageArr = result.split(',,'); //如果数组长度大于0进行数据处理

          if (messageArr.length > 0) {
            //获取状态编码 '1'为成功
            let code = messageArr.splice(0, 1)[0];

            if (code === '1') {
              //请求成功
              //对全部信息数组进行处理
              //messageArr ['痞子#ff8000,刍狗,不会要踢我吧,luGtBJXv','9,我可莉害了,/门派 他就回个有BUG。。。,luGtBJXv']
              messageArr.forEach(itemAll => {
                //itemAll '痞子#ff8000,刍狗,不会要踢我吧,luGtBJXv'
                //将单条信息拆分成单条信息数组
                //singleArr ['痞子#ff8000','刍狗','不会要踢我吧','luGtBJXv']
                let singleArr = itemAll.split(','); //将单条信息数组进行处理 并存储到对象中

                let obj = {
                  chenghao: '',
                  color: '',
                  colorSpecial: '',
                  jingjie: '',
                  daohao: '',
                  whichBox: '',
                  content: '',
                  menpaiCode: '',
                  //如果为0则代表没有门派
                  menpaiName: ''
                };
                singleArr.forEach((itemSingle, index) => {
                  //对第一块数据进行处理
                  if (itemSingle.indexOf('#') !== -1 && index === 0) {
                    //说明第一个位置带称号
                    //痞子#ff8000 将字符串根据#拆分
                    let itemArr = itemSingle.split('#');
                    obj.chenghao = itemArr[0];
                    obj.color = `#${itemArr[1]}`; //进一步的数据处理

                    if (obj.chenghao === '管理员' || obj.chenghao === '系统') {
                      obj.color = '#ff0000';
                      obj.colorSpecial = '#ff0000';
                    } else if (obj.chenghao === '百晓通') {
                      obj.color = '#c000c0';
                      obj.colorSpecial = '#c000c0';
                    } else if (obj.chenghao === '天罚') {
                      obj.color = '#ff8000';
                      obj.colorSpecial = '#ff8000';
                    }
                  } else if (index === 0) {
                    //不带称号
                    //从json中找到对应境界
                    xiuweiJson.forEach(jsonItem => {
                      if (jsonItem.id === itemSingle) {
                        obj.jingjie = jsonItem.name;
                      }
                    });
                  } //对第二块数据进行处理


                  if (index === 1) {
                    obj.daohao = itemSingle;
                  } //对第三块数据进行处理


                  if (index === 2) {
                    if (itemSingle.startsWith('/门派')) {
                      //如果是门派开头的数据
                      obj.whichBox = 'menpai';
                      obj.content = itemSingle.substr(3, itemSingle.length).trimStart();
                    } else {
                      obj.whichBox = 'shijie';
                      obj.content = itemSingle;
                    }
                  } //对第四块内容进行处理


                  if (index === 3) {
                    obj.menpaiCode = itemSingle; //从json中找到对应门派

                    menpaiJson.forEach(jsonItem => {
                      if (jsonItem.id === itemSingle) {
                        obj.menpaiName = jsonItem.name;
                      }
                    });
                  }
                }); //判断放入世界容器还是门派容器

                if (obj.whichBox === 'shijie') {
                  //放入对应盒子
                  store.dispatch('user/handleWorldChat', obj); // shijieChatBox.value.push(obj)
                  // //页面更新完毕后调用
                  // nextTick(()=>{
                  //   //让滚动条滚动endP到视图位置
                  //   endP.value.scrollIntoView()
                  // })
                } else if (obj.whichBox === 'menpai') {
                  //判断是否与自己门派id相同 且不为'0'
                  if (obj.menpaiCode === myMenpaiId.value && myMenpaiId.value !== '0') {
                    //放入对应盒子
                    store.dispatch('user/handleMenpaiChat', obj); // mymenpaiChatBox.value.push(obj)
                    // //页面更新完毕后调用
                    // nextTick(()=>{
                    //   //让滚动条滚动endP到视图位置
                    //   endP.value.scrollIntoView()
                    // })
                  }
                }
              });
            }
          }
        } // console.log(store.state.user.worldChatBox)

      }, 2000);
    };

    onMounted(() => {
      //开启定时器
      startTimer();
    });
  }

};