import { ref } from 'vue';
import { useRouter } from 'vue-router';
import navbarJson from '@/assets/data/navbarData.json';
export default {
  name: 'Navbar',

  setup() {
    //导航条数据
    const navbarData = navbarJson; //当前点击的导航

    let nowNav = ref(1);
    const router = useRouter(); //点击事件

    const btnClick = nav => {
      //存储当前路由导航标识
      nowNav.value = nav.id; //前往目标页面

      router.push(nav.path);
    };

    return {
      navbarData,
      btnClick,
      nowNav
    };
  }

};